.NavItem {
  margin: 10px 0;
  box-sizing: border-box;
  display: block;
  width: 100%;
}

.NavItem a {
  color: white;
  font-size: 1.2em;
  font-weight: bold;
  text-decoration: none;
  width: 100%;
  box-sizing: border-box;
  display: block;
}

@media (min-width:650px) {
  .NavItem {
    margin: 0;
    margin-top: 3px;
    display: flex;
    height: 100%;
    width: auto;
    align-items: center;
  }
  .NavItem a {
    height: 100%;
    padding: 10px 10px;
    margin: 0 2px;
  }
  .NavItem a:hover,
  .NavItem a:active,
  .NavItem a.active {
    color: #2874ff;
    background-color: white;
    border-radius: 7px;
  }
}
