.Container {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}
.NavigationItems {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-flow: column;
  align-items: center;
  height: 100%;
}

@media (min-width: 650px) {
  .NavigationItems {
      flex-flow: row;
  }
}
