.Label {
  width: fit-content;
  color: #444;
  opacity: 0.7;
  font-weight: bold;
  font-size: 1.1em;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow-wrap: break-word;
  word-wrap: break-word;
  hyphens: auto;
}
@media( max-width: 650px ) {
  .Label {
    font-size: 0.9em;
  }
}