.Modal {
  position: fixed;
  z-index: 500;
  background-color: #ffffff;
  width: 80%;
  max-height: 60%;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 1px black;
  padding: 16px;
  left: 10%;
  top: 25%;
  box-sizing: border-box;
  transition: all 0.3s ease-out;
  overflow: auto;
}

@media (min-width: 600px) {
  .Modal {
      width: 500px;
      left: calc(50% - 250px);
  }
}